@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
}

body {
  width: 100%;
  min-height: 100%;
  line-height: 1;
  font-family: sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}

input,
select,
option,
optgroup,
textarea,
button,
pre,
code {
  font-size: 100%;
  font-family: inherit;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

/* Root */

:root {
  --gradient: linear-gradient(90deg, #ee6352, purple, #ee6352);
}

/* Header Styling */

.header {
  display: flex;
  padding: 1%;
}

.header .left,
.header .right,
.header .center {
  width: 33.333%;
}

.navBtn {
  margin: 20px;
  border: none;
  background: rgba(255, 255, 255, 0);
  z-index: 10;
  position: relative;
}

.navBtn div {
  background: #000;
  border-radius: 3px;
  height: 3px;
  transform: none;
  transform-origin: left;
  transition: transform 500ms cubic-bezier(0, 0.61, 0.28, 0.92);
  width: 32px;
  margin-top: 8px;
  transform-origin: center;
  box-shadow: 0px 0px 5px white;
}

.header .center {
  font-family: "Caveat";
  font-size: 50px;
  width: 100%;
  text-align: center;
  background: rgb(13, 184, 255);
  background: linear-gradient(
    90deg,
    rgba(13, 184, 255, 1) 0%,
    rgba(243, 50, 255, 1) 25%,
    rgba(60, 255, 83, 1) 50%,
    rgba(57, 174, 255, 1) 75%,
    rgba(137, 39, 143, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Caveat";
  background-size: 300%;
  animation: bg-animation 1s infinite;
}

/* 205, 64, 143 */
/* 9, 79, 195 */

.fullscreenNav {
  opacity: 0;
  height: 0px;
  padding: 0px;
  overflow: hidden;
  transition: all 0.3s ease 0.15s;
  background: linear-gradient(
    90deg,
    rgba(205, 64, 143, 1),
    rgba(9, 79, 195, 1)
  );
  width: 100vw;
  margin: -100px 0px;
  z-index: 9;
  position: relative;
}

.navLinks {
  font-size: 80px;
  font-family: "Roboto", sans-serif;
}

.navLinks a h1 {
  margin-top: 50px;
  width: max-content;
  position: relative;
}

.navLinks a h1:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 5px;
  bottom: 0;
  left: 0;
  background-color: #fff;
  transform-origin: bottom right;
  transition: transform 500ms ease-out;
}

.navLinks a h1:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

@keyframes bg-animation {
  0% {
    background-position: left;
  }
  25% {
    background-position: top;
  }
  50% {
    background-position: right;
  }
  100% {
    background-position: bottom;
  }
}

/* Banner */

[class*="banner-one__shape-"] {
  position: absolute;
  border-radius: 50%;
  -webkit-animation-name: bubbleMover;
  animation-name: bubbleMover;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  background-size: 300%;
}

.banner-one__shape-1 {
  background-image: linear-gradient(40deg, #0db8ff 0%, #f332ff 100%);
  -webkit-box-shadow: 0px 20px 60px 0px rgba(141, 142, 255, 0.8);
  box-shadow: 0px 20px 60px 0px rgba(141, 142, 255, 0.8);
  width: 143px;
  height: 143px;
  top: 5%;
  left: 3%;
  -webkit-animation-duration: 20s;
  animation-duration: 20s;
  -webkit-perspective: 150px;
  perspective: 150px;
}

.banner-one__shape-2 {
  background-image: linear-gradient(40deg, #39aeff 0%, #3cff53 100%);
  -webkit-box-shadow: 0px 20px 40px 0px rgba(58, 212, 173, 0.8);
  box-shadow: 0px 20px 40px 0px rgba(58, 212, 173, 0.8);
  width: 85px;
  height: 85px;
  top: 12%;
  left: 37%;
  -webkit-animation-duration: 20s;
  animation-duration: 20s;
  -webkit-perspective: 85px;
  perspective: 85px;
}

.banner-one__shape-3 {
  background-image: linear-gradient(40deg, #39aeff 0%, #3cff53 100%);
  -webkit-box-shadow: 0px 20px 40px 0px rgba(58, 212, 173, 0.8);
  box-shadow: 0px 20px 40px 0px rgba(58, 212, 173, 0.8);
  width: 85px;
  height: 85px;
  bottom: 12%;
  left: 6%;
  -webkit-animation-duration: 20s;
  animation-duration: 20s;
  -webkit-perspective: 85px;
  perspective: 85px;
}

.banner-one__shape-4 {
  background-image: linear-gradient(40deg, #0db8ff 0%, #f332ff 100%);
  -webkit-box-shadow: 0px 20px 60px 0px rgba(141, 142, 255, 0.8);
  box-shadow: 0px 20px 60px 0px rgba(141, 142, 255, 0.8);
  width: 85px;
  height: 85px;
  top: 20%;
  right: 4%;
  -webkit-animation-duration: 20s;
  animation-duration: 20s;
  -webkit-perspective: 85px;
  perspective: 85px;
}



@keyframes bubbleMover {
  0% {
    -webkit-transform: translateY(0px) translateX(0) rotate(0);
    transform: translateY(0px) translateX(0) rotate(0);
    background-position: left;
  }
  30% {
    -webkit-transform: translateY(30px) translateX(50px) rotate(15deg);
    transform: translateY(30px) translateX(50px) rotate(15deg);
    -webkit-transform-origin: center center;
    transform-origin: center center;
    background-position: top;
  }
  50% {
    -webkit-transform: translateY(50px) translateX(100px) rotate(45deg);
    transform: translateY(50px) translateX(100px) rotate(45deg);
    -webkit-transform-origin: right bottom;
    transform-origin: right bottom;
    background-position: right;
  }
  80% {
    -webkit-transform: translateY(30px) translateX(50px) rotate(15deg);
    transform: translateY(30px) translateX(50px) rotate(15deg);
    -webkit-transform-origin: left top;
    transform-origin: left top;
    background-position: bottom;
  }
  100% {
    -webkit-transform: translateY(0px) translateX(0) rotate(0);
    transform: translateY(0px) translateX(0) rotate(0);
    -webkit-transform-origin: center center;
    transform-origin: center center;
  }
}

/*
* 3. banner styles
*/
.banner-one {
  position: relative;
  background-image: url(../images/background/banner-bg-1-1.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}
.banner-one .container {
  padding-top: 350px;
  padding-bottom: 230px;
  position: relative;
  margin: 75px 360px
}
.banner-one__title {
  margin: 0;
  color: #2a2833;
  font-weight: 300;
  font-size: 90px;
  line-height: 1em;
  letter-spacing: -0.06em;
}
.banner-one__title span {
  font-weight: 500;
}
.banner-one__text {
  margin: 0;
  font-size: 20px;
  line-height: 36px;
  color: #74727a;
  margin-top: 25px;
  margin-bottom: 40px;
}
.banner-one__btn {
  padding: 18px 55px;
}
.banner-one__moc {
  position: absolute;
  bottom: 7%;
  right: -30px;
}
.banner-one__moc:before {
  content: "";
  width: 639px;
  height: 639px;
  background-image: linear-gradient(40deg, #ff43c0 0%, #ffa95c 100%);
  -webkit-box-shadow: 0px 20px 60px 0px rgba(244, 151, 190, 0.8);
  box-shadow: 0px 20px 60px 0px rgba(244, 151, 190, 0.8);
  border-radius: 50%;
  position: absolute;
  top: -14%;
  left: -24%;
  -webkit-transform-origin: center bottom;
  transform-origin: center bottom;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: bounce;
  animation-name: bounce;
  -webkit-animation-timing-function: ease-out;
  animation-timing-function: ease-out;
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.banner-one__moc img {
  position: relative;
  -webkit-animation: featureImgBounce 5s ease-in-out 0s infinite alternate;
  animation: featureImgBounce 5s ease-in-out 0s infinite alternate;
}
